import React, { useState, useRef, useEffect } from 'react';
import { Trophy, Users, ChevronRight, Play, Crown } from 'lucide-react';

// Your existing PingPongGame component code (simplified for demo)
const PingPongGame = ({ player1, player2, onGameEnd, onExit }) => {
  const canvasRef = useRef(null);
  const [scores, setScores] = useState({ p1: 0, p2: 0 });
  const [gameStarted, setGameStarted] = useState(false);
  const [paused, setPaused] = useState(false);
  const [gameTime, setGameTime] = useState({ minutes: 0, seconds: 0 });
  
  // Simplified game constants
  const GAME_WIDTH = 800;
  const GAME_HEIGHT = 400;
  const PADDLE_WIDTH = 12;
  const PADDLE_HEIGHT = 80;
  const BALL_SIZE = 12;
  const PADDLE_SPEED = 6;
  const BALL_SPEED = 5;

  const paddle1Y = useRef(GAME_HEIGHT / 2 - PADDLE_HEIGHT / 2);
  const paddle2Y = useRef(GAME_HEIGHT / 2 - PADDLE_HEIGHT / 2);
  const ball = useRef({
    x: GAME_WIDTH / 2,
    y: GAME_HEIGHT / 2,
    dx: BALL_SPEED * (Math.random() > 0.5 ? 1 : -1),
    dy: BALL_SPEED * (Math.random() > 0.5 ? 1 : -1),
  });
  const keys = useRef({});

  // Game timer
  useEffect(() => {
    let interval;
    if (gameStarted && !paused) {
      interval = setInterval(() => {
        setGameTime(prev => {
          const newSeconds = prev.seconds + 1;
          if (newSeconds >= 60) {
            return { minutes: prev.minutes + 1, seconds: 0 };
          }
          return { ...prev, seconds: newSeconds };
        });
      }, 1000);
    }
    return () => clearInterval(interval);
  }, [gameStarted, paused]);

  // Keyboard controls
  useEffect(() => {
    const downHandler = (e) => {
      keys.current[e.key.toLowerCase()] = true;
    };
    const upHandler = (e) => {
      keys.current[e.key.toLowerCase()] = false;
    };
    window.addEventListener("keydown", downHandler);
    window.addEventListener("keyup", upHandler);
    return () => {
      window.removeEventListener("keydown", downHandler);
      window.removeEventListener("keyup", upHandler);
    };
  }, []);

  // Game loop
  useEffect(() => {
    if (!gameStarted || paused) return;
    let animation;

    const draw = () => {
      const ctx = canvasRef.current?.getContext("2d");
      if (!ctx) return;
      
      // Clear canvas
      ctx.clearRect(0, 0, GAME_WIDTH, GAME_HEIGHT);
      
      // Background
      ctx.fillStyle = "#1a1a2e";
      ctx.fillRect(0, 0, GAME_WIDTH, GAME_HEIGHT);

      // Center line
      ctx.strokeStyle = "rgba(255,255,255,0.3)";
      ctx.setLineDash([5, 5]);
      ctx.beginPath();
      ctx.moveTo(GAME_WIDTH / 2, 0);
      ctx.lineTo(GAME_WIDTH / 2, GAME_HEIGHT);
      ctx.stroke();
      ctx.setLineDash([]);

      // Paddles
      ctx.fillStyle = "#ffffff";
      ctx.fillRect(20, paddle1Y.current, PADDLE_WIDTH, PADDLE_HEIGHT);
      ctx.fillRect(GAME_WIDTH - 32, paddle2Y.current, PADDLE_WIDTH, PADDLE_HEIGHT);

      // Ball
      ctx.beginPath();
      ctx.arc(ball.current.x, ball.current.y, BALL_SIZE / 2, 0, Math.PI * 2);
      ctx.fill();
    };

    const update = () => {
      // Paddle movement
      if (keys.current["w"] && paddle1Y.current > 0) paddle1Y.current -= PADDLE_SPEED;
      if (keys.current["s"] && paddle1Y.current < GAME_HEIGHT - PADDLE_HEIGHT) paddle1Y.current += PADDLE_SPEED;
      if (keys.current["arrowup"] && paddle2Y.current > 0) paddle2Y.current -= PADDLE_SPEED;
      if (keys.current["arrowdown"] && paddle2Y.current < GAME_HEIGHT - PADDLE_HEIGHT) paddle2Y.current += PADDLE_SPEED;

      // Ball movement
      ball.current.x += ball.current.dx;
      ball.current.y += ball.current.dy;

      // Wall collision
      if (ball.current.y <= BALL_SIZE/2 || ball.current.y >= GAME_HEIGHT - BALL_SIZE/2) {
        ball.current.dy *= -1;
      }

      // Paddle collision
      if (ball.current.x <= 32 + BALL_SIZE/2 && 
          ball.current.y >= paddle1Y.current && 
          ball.current.y <= paddle1Y.current + PADDLE_HEIGHT) {
        ball.current.dx = Math.abs(ball.current.dx);
      }
      
      if (ball.current.x >= GAME_WIDTH - 32 - BALL_SIZE/2 && 
          ball.current.y >= paddle2Y.current && 
          ball.current.y <= paddle2Y.current + PADDLE_HEIGHT) {
        ball.current.dx = -Math.abs(ball.current.dx);
      }

      // Scoring
      if (ball.current.x < 0) {
        setScores(s => ({ ...s, p2: s.p2 + 1 }));
        resetBall();
      } else if (ball.current.x > GAME_WIDTH) {
        setScores(s => ({ ...s, p1: s.p1 + 1 }));
        resetBall();
      }
    };

    const resetBall = () => {
      ball.current.x = GAME_WIDTH / 2;
      ball.current.y = GAME_HEIGHT / 2;
      ball.current.dx = BALL_SPEED * (Math.random() > 0.5 ? 1 : -1);
      ball.current.dy = BALL_SPEED * (Math.random() > 0.5 ? 1 : -1);
    };

    const loop = () => {
      update();
      draw();
      animation = requestAnimationFrame(loop);
    };

    loop();
    return () => cancelAnimationFrame(animation);
  }, [gameStarted, paused]);

  // Win condition check
  useEffect(() => {
    if (scores.p1 >= 7 || scores.p2 >= 7) {
      const winner = scores.p1 >= 7 ? player1 : player2;
      const loser = scores.p1 >= 7 ? player2 : player1;
      onGameEnd(winner, loser, scores, gameTime);
    }
  }, [scores, player1, player2, onGameEnd, gameTime]);

  const handleStart = () => {
    setScores({ p1: 0, p2: 0 });
    setGameTime({ minutes: 0, seconds: 0 });
    paddle1Y.current = GAME_HEIGHT / 2 - PADDLE_HEIGHT / 2;
    paddle2Y.current = GAME_HEIGHT / 2 - PADDLE_HEIGHT / 2;
    ball.current.x = GAME_WIDTH / 2;
    ball.current.y = GAME_HEIGHT / 2;
    ball.current.dx = BALL_SPEED * (Math.random() > 0.5 ? 1 : -1);
    ball.current.dy = BALL_SPEED * (Math.random() > 0.5 ? 1 : -1);
    setGameStarted(true);
    setPaused(false);
  };

  return (
    <div className="bg-gray-900 min-h-screen flex flex-col">
      <div className="flex-1 flex items-center justify-center p-4">
        <div className="bg-gray-800 rounded-lg p-6 shadow-2xl">
          {/* Game Header */}
          <div className="mb-4 flex justify-between items-center">
            <div className="flex items-center gap-4">
              <div className="text-center">
                <img src={player1.avatar} alt={player1.name} className="w-12 h-12 rounded-full mb-2" />
                <div className="text-white font-bold">{player1.name}</div>
                <div className="text-2xl font-bold text-blue-400">{scores.p1}</div>
              </div>
              <div className="text-white text-xl">VS</div>
              <div className="text-center">
                <img src={player2.avatar} alt={player2.name} className="w-12 h-12 rounded-full mb-2" />
                <div className="text-white font-bold">{player2.name}</div>
                <div className="text-2xl font-bold text-red-400">{scores.p2}</div>
              </div>
            </div>
            <div className="text-white">
              {String(gameTime.minutes).padStart(2, '0')}:{String(gameTime.seconds).padStart(2, '0')}
            </div>
          </div>

          {/* Game Canvas */}
          <div className="relative">
            <canvas
              ref={canvasRef}
              width={GAME_WIDTH}
              height={GAME_HEIGHT}
              className="border border-gray-700 rounded"
            />
            
            {!gameStarted && (
              <div className="absolute inset-0 flex items-center justify-center bg-black bg-opacity-50 rounded">
                <button
                  onClick={handleStart}
                  className="bg-green-600 hover:bg-green-700 text-white px-8 py-4 rounded-lg font-bold text-xl flex items-center gap-2"
                >
                  <Play size={24} />
                  Start Game
                </button>
              </div>
            )}
          </div>

          {/* Controls */}
          <div className="mt-4 text-center text-gray-400 text-sm">
            Player 1: W/S keys | Player 2: Arrow keys
          </div>

          {/* Game Controls */}
          <div className="mt-4 flex justify-center gap-4">
            {gameStarted && (
              <button
                onClick={() => setPaused(!paused)}
                className="bg-yellow-600 hover:bg-yellow-700 text-white px-4 py-2 rounded"
              >
                {paused ? 'Resume' : 'Pause'}
              </button>
            )}
            <button
              onClick={onExit}
              className="bg-red-600 hover:bg-red-700 text-white px-4 py-2 rounded"
            >
              Exit to Tournament
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

// Tournament System Component
const TournamentSystem = () => {
  const [currentView, setCurrentView] = useState('setup'); // 'setup', 'bracket', 'game', 'final'
  const [tournament, setTournament] = useState(null);
  const [currentMatch, setCurrentMatch] = useState(null);
  const [gameHistory, setGameHistory] = useState([]);

  // Sample players (you can replace with your actual player data)
  const samplePlayers = [
    { id: 1, name: "Alex Johnson", nickname: "alexj", avatar: "https://images.unsplash.com/photo-1472099645785-5658abf4ff4e?w=100&h=100&fit=crop&crop=face" },
    { id: 2, name: "Maria Garcia", nickname: "mariag", avatar: "https://images.unsplash.com/photo-1494790108755-2616b612b05b?w=100&h=100&fit=crop&crop=face" },
    { id: 3, name: "James Wilson", nickname: "jwilson", avatar: "https://images.unsplash.com/photo-1507003211169-0a1dd7228f2d?w=100&h=100&fit=crop&crop=face" },
    { id: 4, name: "Sarah Davis", nickname: "sarahd", avatar: "https://images.unsplash.com/photo-1438761681033-6461ffad8d80?w=100&h=100&fit=crop&crop=face" },
    { id: 5, name: "Mike Brown", nickname: "mikeb", avatar: "https://images.unsplash.com/photo-1500648767791-00dcc994a43e?w=100&h=100&fit=crop&crop=face" },
    { id: 6, name: "Lisa Zhang", nickname: "lisaz", avatar: "https://images.unsplash.com/photo-1544725176-7c40e5a71c5e?w=100&h=100&fit=crop&crop=face" },
    { id: 7, name: "David Lee", nickname: "davidl", avatar: "https://images.unsplash.com/photo-1522075469751-3a6694fb2f61?w=100&h=100&fit=crop&crop=face" },
    { id: 8, name: "Emma Taylor", nickname: "emmat", avatar: "https://images.unsplash.com/photo-1487412720507-e7ab37603c6f?w=100&h=100&fit=crop&crop=face" }
  ];

  const [selectedPlayers, setSelectedPlayers] = useState([]);

  const createTournament = (players) => {
    if (players.length < 2) return null;
    
    // Ensure we have a power of 2 number of players for clean brackets
    const numPlayers = players.length;
    const rounds = Math.ceil(Math.log2(numPlayers));
    
    return {
      players: [...players],
      rounds: rounds,
      currentRound: 1,
      matches: generateMatches(players),
      winners: [],
      champion: null
    };
  };

  const generateMatches = (players) => {
    const matches = [];
    for (let i = 0; i < players.length; i += 2) {
      if (i + 1 < players.length) {
        matches.push({
          id: `match-${i/2}`,
          player1: players[i],
          player2: players[i + 1],
          winner: null,
          completed: false,
          round: 1
        });
      }
    }
    return matches;
  };

  const startTournament = () => {
    if (selectedPlayers.length < 2) {
      alert('Please select at least 2 players');
      return;
    }
    
    const newTournament = createTournament(selectedPlayers);
    setTournament(newTournament);
    setCurrentView('bracket');
  };

  const startMatch = (match) => {
    setCurrentMatch(match);
    setCurrentView('game');
  };

  const handleGameEnd = (winner, loser, scores, gameTime) => {
    const matchResult = {
      ...currentMatch,
      winner: winner,
      loser: loser,
      scores: scores,
      gameTime: gameTime,
      completed: true,
      timestamp: new Date()
    };

    // Add to game history
    setGameHistory(prev => [...prev, matchResult]);

    // Update tournament
    setTournament(prev => {
      const updatedMatches = prev.matches.map(match => 
        match.id === currentMatch.id ? matchResult : match
      );

      const currentRoundMatches = updatedMatches.filter(m => m.round === prev.currentRound);
      const completedCurrentRound = currentRoundMatches.every(m => m.completed);

      if (completedCurrentRound) {
        const winners = currentRoundMatches.map(m => m.winner);
        
        if (winners.length === 1) {
          // Tournament is complete
          return {
            ...prev,
            matches: updatedMatches,
            champion: winners[0],
            winners: [...prev.winners, ...winners]
          };
        } else {
          // Create next round
          const nextRoundMatches = [];
          for (let i = 0; i < winners.length; i += 2) {
            if (i + 1 < winners.length) {
              nextRoundMatches.push({
                id: `match-r${prev.currentRound + 1}-${i/2}`,
                player1: winners[i],
                player2: winners[i + 1],
                winner: null,
                completed: false,
                round: prev.currentRound + 1
              });
            }
          }

          return {
            ...prev,
            matches: [...updatedMatches, ...nextRoundMatches],
            currentRound: prev.currentRound + 1,
            winners: [...prev.winners, ...winners]
          };
        }
      }

      return {
        ...prev,
        matches: updatedMatches
      };
    });

    setCurrentView('bracket');
  };

  const resetTournament = () => {
    setTournament(null);
    setCurrentMatch(null);
    setGameHistory([]);
    setSelectedPlayers([]);
    setCurrentView('setup');
  };

  if (currentView === 'game' && currentMatch) {
    return (
      <PingPongGame
        player1={currentMatch.player1}
        player2={currentMatch.player2}
        onGameEnd={handleGameEnd}
        onExit={() => setCurrentView('bracket')}
      />
    );
  }

  return (
    <div className="bg-gray-900 min-h-screen text-white">
      {/* Header */}
      <header className="bg-gray-800 p-4 shadow-lg">
        <div className="max-w-6xl mx-auto flex items-center justify-between">
          <div className="flex items-center gap-3">
            <Trophy className="text-yellow-500" size={32} />
            <h1 className="text-2xl font-bold">Ping Pong Tournament</h1>
          </div>
          {tournament && (
            <button
              onClick={resetTournament}
              className="bg-red-600 hover:bg-red-700 px-4 py-2 rounded transition-colors"
            >
              Reset Tournament
            </button>
          )}
        </div>
      </header>

      <div className="max-w-6xl mx-auto p-6">
        {currentView === 'setup' && (
          <div className="space-y-6">
            <div className="text-center">
              <h2 className="text-3xl font-bold mb-2">Setup Tournament</h2>
              <p className="text-gray-400">Select players to participate in the tournament</p>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
              {samplePlayers.map(player => (
                <div
                  key={player.id}
                  className={`p-4 rounded-lg border-2 cursor-pointer transition-all ${
                    selectedPlayers.some(p => p.id === player.id)
                      ? 'border-blue-500 bg-blue-500/20'
                      : 'border-gray-700 bg-gray-800 hover:border-gray-600'
                  }`}
                  onClick={() => {
                    if (selectedPlayers.some(p => p.id === player.id)) {
                      setSelectedPlayers(prev => prev.filter(p => p.id !== player.id));
                    } else {
                      setSelectedPlayers(prev => [...prev, player]);
                    }
                  }}
                >
                  <img src={player.avatar} alt={player.name} className="w-16 h-16 rounded-full mx-auto mb-3" />
                  <h3 className="font-bold text-center">{player.name}</h3>
                  <p className="text-gray-400 text-center text-sm">@{player.nickname}</p>
                </div>
              ))}
            </div>

            <div className="text-center">
              <p className="mb-4 text-gray-400">
                Selected: {selectedPlayers.length} players
              </p>
              <button
                onClick={startTournament}
                disabled={selectedPlayers.length < 2}
                className="bg-green-600 hover:bg-green-700 disabled:bg-gray-600 px-8 py-3 rounded-lg font-bold text-lg transition-colors flex items-center gap-2 mx-auto"
              >
                <Users size={20} />
                Start Tournament
              </button>
            </div>
          </div>
        )}

        {currentView === 'bracket' && tournament && (
          <div className="space-y-6">
            <div className="text-center">
              <h2 className="text-3xl font-bold mb-2">Tournament Bracket</h2>
              <p className="text-gray-400">Round {tournament.currentRound}</p>
              {tournament.champion && (
                <div className="mt-4 p-6 bg-yellow-500/20 border border-yellow-500 rounded-lg">
                  <Crown className="text-yellow-500 mx-auto mb-2" size={48} />
                  <h3 className="text-2xl font-bold text-yellow-500">Tournament Champion!</h3>
                  <div className="flex items-center justify-center gap-3 mt-3">
                    <img src={tournament.champion.avatar} alt={tournament.champion.name} className="w-16 h-16 rounded-full" />
                    <div>
                      <div className="text-xl font-bold">{tournament.champion.name}</div>
                      <div className="text-gray-400">@{tournament.champion.nickname}</div>
                    </div>
                  </div>
                </div>
              )}
            </div>

            <div className="space-y-4">
              {[...Array(tournament.rounds)].map((_, roundIndex) => {
                const roundNum = roundIndex + 1;
                const roundMatches = tournament.matches.filter(m => m.round === roundNum);
                
                if (roundMatches.length === 0) return null;

                return (
                  <div key={roundNum} className="bg-gray-800 p-4 rounded-lg">
                    <h3 className="text-xl font-bold mb-4 text-center">
                      {roundNum === tournament.rounds ? 'Final' : `Round ${roundNum}`}
                    </h3>
                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                      {roundMatches.map(match => (
                        <div key={match.id} className="bg-gray-700 p-4 rounded-lg">
                          <div className="flex items-center justify-between mb-3">
                            <div className="flex items-center gap-2">
                              <img src={match.player1.avatar} alt={match.player1.name} className="w-8 h-8 rounded-full" />
                              <span className={match.winner?.id === match.player1.id ? 'font-bold text-green-400' : ''}>{match.player1.name}</span>
                            </div>
                            {match.completed && (
                              <span className="text-sm font-mono">
                                {match.scores?.p1} - {match.scores?.p2}
                              </span>
                            )}
                          </div>
                          <div className={`text-center text-gray-400 mb-3 ${match.winner ? 'text-green-400' : ''}`}>
                            {match.completed ? '✓' : 'VS'}
                          </div>
                          <div className="flex items-center justify-between mb-3">
                            <div className="flex items-center gap-2">
                              <img src={match.player2.avatar} alt={match.player2.name} className="w-8 h-8 rounded-full" />
                              <span className={match.winner?.id === match.player2.id ? 'font-bold text-green-400' : ''}>{match.player2.name}</span>
                            </div>
                          </div>
                          {!match.completed && roundNum === tournament.currentRound && (
                            <button
                              onClick={() => startMatch(match)}
                              className="w-full bg-blue-600 hover:bg-blue-700 py-2 rounded transition-colors flex items-center justify-center gap-2"
                            >
                              <Play size={16} />
                              Play Match
                            </button>
                          )}
                        </div>
                      ))}
                    </div>
                  </div>
                );
              })}
            </div>

            {gameHistory.length > 0 && (
              <div className="bg-gray-800 p-4 rounded-lg">
                <h3 className="text-xl font-bold mb-4">Match History</h3>
                <div className="space-y-2">
                  {gameHistory.map((game, index) => (
                    <div key={index} className="bg-gray-700 p-3 rounded flex items-center justify-between">
                      <div className="flex items-center gap-3">
                        <img src={game.winner.avatar} alt={game.winner.name} className="w-8 h-8 rounded-full" />
                        <span className="font-bold text-green-400">{game.winner.name}</span>
                        <span className="text-gray-400">defeated</span>
                        <img src={game.loser.avatar} alt={game.loser.name} className="w-8 h-8 rounded-full" />
                        <span>{game.loser.name}</span>
                      </div>
                      <div className="text-sm text-gray-400">
                        {game.scores.p1} - {game.scores.p2} | {game.gameTime.minutes}:{String(game.gameTime.seconds).padStart(2, '0')}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default TournamentSystem;